import "./App.css";
import { Button } from "@nextui-org/react";
import { Accordion, AccordionItem } from "@nextui-org/react";

const MainPage = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };
  const defaultContent =
    "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.";

  return (
    <>
      <div
        className="main-container"
        style={{
          backgroundColor: "#14141A",
          padding: "20px",
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          justifyContent: "center",
        }}
      >
        <a href="#"> Koh Aesthetics </a>

        <div
          className="app-button"
          style={{
            backgroundColor: "#c4c2b1",
            color: "#000000",
            padding: "20px",
            borderRadius: "10px",
            border: "2px solid #050511",
            textAlign: "center",
            maxWidth: "450px",
            width: "100%",
          }}
        >
          <h1
            style={{ fontSize: "20px", marginBottom: "10px", color: "#0f1014" }}
          >
            Welcome to Koh Aesthetics!
          </h1>
          <p
            style={{ fontSize: "15px", marginBottom: "20px", color: "#0f1014" }}
          >
            Welcome to Dr. Koh Aesthetics! We're here to enhance your natural
            beauty with personalized care. Dr. Koh is dedicated to providing
            individualized aesthetic solutions that highlight your unique
            features. Interested in a personalized consultation? Book an
            appointment today!
          </p>
          <Button
            disableRipple
            className="relative overflow-visible rounded-full hover:-translate-y-1 px-12 shadow-xl bg-background/30 after:content-[''] after:absolute after:rounded-full after:inset-0 after:bg-background/40 after:z-[-1] after:transition after:!duration-500 hover:after:scale-150 hover:after:opacity-0"
            onClick={redirectToLink}
            style={{
              backgroundColor: "#0f1014",
              color: "#f9f9fb",
              fontWeight: "500",
              width: "100%",
            }}
          >
            Book an Appointment
          </Button>
        </div>
        <Accordion
          styles={{
            background: "#898567", // Background color for the accordion container
            color: "#0f1014", // Text color for the accordion container
          }}
        >
          <AccordionItem key="1" aria-label="Injectables" title="Injectables">
            {defaultContent}
          </AccordionItem>
          <AccordionItem key="2" aria-label="PDO Threads" title="Accordion 2">
            {defaultContent}
          </AccordionItem>
          <AccordionItem key="3" aria-label="IV Infusion" title="Accordion 3">
            {defaultContent}
          </AccordionItem>
          <AccordionItem key="4" aria-label="Microneedling" title="Accordion 4">
            {defaultContent}
          </AccordionItem>
          <AccordionItem key="5" aria-label="Consultation" title="Accordion 5">
            {defaultContent}
          </AccordionItem>
          <AccordionItem
            key="6"
            aria-label="Weight Loss Program"
            title="Accordion 6"
          >
            {defaultContent}
          </AccordionItem>
          <AccordionItem
            key="7"
            aria-label="Hair Restoration"
            title="Accordion 7"
          >
            {defaultContent}
          </AccordionItem>
        </Accordion>
        <h2 style={{ color: "#f9f9fb" }}>
          Dr.Koh's web is still a work in progress, so be sure to come back when
          it's finished!
        </h2>
      </div>
    </>
  );
};

export default MainPage;
